.each_attr_Wrap{
    background: #f2f2f2;
    padding: 10px;
}
.arInputWrapper{
    cursor: pointer;
}
.searchReference{
    list-style: none;
    padding: 0;
    margin: 0;
    box-shadow: 0 0 10px rgba(0,0,0,0.2);
    max-height: 300px;
    overflow-y: auto;
    li{
        padding: 8px 12px;
        cursor: pointer;
    }
}
.selectedProducts{
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    margin: 0 -6px 10px;
    &:empty{
        display: none;
    }
    li{
        padding: 0 6px;
        span{
            padding: 6px 12px;
            display: block;
            border: 1px solid #ddd;
            position: relative;
            svg{
                cursor: pointer;
                position: absolute;
                top: -8px;
                right: -8px;
                width: 16px;
                height: 16px;
                background: #9b4dff;
                color: #fff;
                border-radius: 50%;
                padding: 2px;
            }
        }
        
    }
}